export default (state, action) => {
    switch (action.type) {
        case 'setHat':
        return {
            ...state,
            currentHat: action.value
        };
        case 'changeHat':
            return {
                ...state,
                currentHat: state.currentHat + action.value
            };
        case 'setScepter':
            return {
                ...state,
                currentScepter: action.value
            };
        case 'changeScepter':
            return {
                ...state,
                currentScepter: state.currentScepter + action.value
            };
        case 'ready':
            return {
                ...state,
                ready: action.value
            };
        case 'setColor':
            return {
                ...state,
                color: action.value
            };
        case 'setPlayerName':
            return {
                ...state,
                playerName: action.value
            };
        default:
            return state;
    }
};